import { Component, ElementRef, EventEmitter, Inject, Output, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { BaseDirective } from 'src/app/shared/base.directive';
import { DialogEvent, HashMap } from 'src/app/shared/utilities/types.utilities';
import { BookingRuleDetails, Building } from 'src/app/services/data/organisation/building.class';


export interface BookingRulesModalData {
    building: Building;
}

@Component({
    selector: 'booking-rules-modal',
    templateUrl: './booking-rules-modal.template.html',
    styleUrls: ['./booking-rules-modal.styles.scss'],
})
export class BookingRulesModalComponent extends BaseDirective {
    /** Emitter for events on the modal */
    @Output() public event = new EventEmitter<DialogEvent>();

    @ViewChild('content', { static: true }) private _content_el: ElementRef<HTMLElement>;

    constructor(@Inject(MAT_DIALOG_DATA) private _data: BookingRulesModalData) {
        super();
    }

    public ngOnInit() {
        this.interval('scroll', () => {
            if (this._content_el) this._content_el.nativeElement.scrollTop = 0;
        }, 10);
        this.timeout('scroll', () => this.clearInterval('scroll'), 201);
    }

    /** Name of the set building */
    public get location(): string {
        const building: Building = this._data.building;
        return building.name || '';
    }

    /** Booking rule details */
    public get rules(): string[] {
        const building: Building = this._data.building;
        const details: BookingRuleDetails = building.booking_details;
        return details.rules || [] as any;
    }

    /** Booking rule details */
    public get info(): string {
        const building: Building = this._data.building;
        const details: BookingRuleDetails = building.booking_details;
        return details.info || '';
    }

    public get customHtml(): string | undefined {
        const building: Building = this._data.building;
        const details: BookingRuleDetails = building.booking_details;
        return details.custom_html;
    }

    /** Booking map rule details */
    public get map_rules(): string[] {
        const building: Building = this._data.building;
        const details: BookingRuleDetails = building.booking_details;
        return details.map_rules || [] as any;
    }

    /** Other special booking rules */
    public get other_rules(): { title: string, rules: string[] }[] {
        const building: Building = this._data.building;
        const details: BookingRuleDetails = building.booking_details;
        const rules: HashMap = (details as any).other_rules || {};
        return Object.keys(rules).reduce((a, v) => {
            a.push({ title: v, rules: rules[v] || [] });
            return a;
        }, []);
    }

    public get contact(): string {
        const building: Building = this._data.building;
        const details: BookingRuleDetails = building.booking_details;
        return details.contact || '';
    }

    public get link(): { url?: string, name?: string } {
        const building: Building = this._data.building;
        const details: BookingRuleDetails = building.booking_details;
        return details.link || {};
    }
}
